import {
	recipeObject,
	groupItemsByIntegration,
	filterAutomatorItemsByRecipeType,
	isDefined,
	isEmpty
}   from './components/Utilities.js';

import Publish            from './components/Publish.js';
import Triggers           from './components/Triggers.js';
import Actions            from './components/Actions.js';
import SendFeedback       from './components/SendFeedback.js';
import RecipeTypeSelector from './components/RecipeTypeSelector.js';
import RecipeTitle        from './components/RecipeTitle.js';
import Taxonomies         from './components/Taxonomies.js';

// Load static resources
import './css/uncannyowl-automator.svg';
import './css/uncannyowl-automator.ttf';
import './css/uncannyowl-automator.woff';
import './css/main.scss';

// Get recipe items on page load
recipeObject( 'create', { allRecipes: UncannyAutomator.recipes_object });
delete UncannyAutomator.recipes_object;

// Create UI
new SendFeedback();
new RecipeTitle();
new Taxonomies();

// Check if WP is ready
if ( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp ){
	// Define recipe type manually
	UncannyAutomator.recipe.type = 'user';

	// Group the triggers and actions by integration
	groupItemsByIntegration();

	// Start
	new Triggers();
	new Actions();
	new Publish();
}
else {
	if ( ! isEmpty( UncannyAutomator.recipe.type ) ){
		// Filter Automator items
		filterAutomatorItemsByRecipeType();

		// Group the triggers and actions by integration
		groupItemsByIntegration();

		// Init UI
		new Triggers();
		new Actions();
		new Publish();
	}
	else {
		// Call to Recipe Type Selector
		new RecipeTypeSelector(() => {
			// Filter Automator items
			filterAutomatorItemsByRecipeType();

			// Group the triggers and actions by integration
			groupItemsByIntegration();

			// Init UI
			new Triggers();
			new Actions();
			new Publish();
		});
	}
}


